<template>
  <div>
    <div class="cropper-content">
      <div class="cropper">
        <vueCropper
          ref="cropper"
          :img="cutMyAvatar"
          :outputSize="option.size"
          :outputType="option.outputType"
          :info="true"
          :full="option.full"
          :canMove="option.canMove"
          :canMoveBox="option.canMoveBox"
          :original="option.original"
          :autoCrop="option.autoCrop"
          :fixed="option.fixed"
          :fixedNumber="option.fixedNumber"
          :centerBox="option.centerBox"
          :infoTrue="option.infoTrue"
          :fixedBox="option.fixedBox"
        ></vueCropper>
      </div>
    </div>
    <div class="button-box">
      <van-button @click="finish" type="info"></van-button>
      <van-button type="default" @click="vclose"></van-button>
    </div>
  </div>
</template>

<script>
import { VueCropper } from 'vue-cropper';
import { Button } from 'vant';

export default {
  props: ['cutMyAvatar', 'cutAvatarShow', 'avatarUrl'],
  name: '',
  data() {
    return {
      // option
      option: {
        info: true, // 
        outputSize: 0.8, // 
        outputType: 'jpeg', // 
        canScale: false, // 
        autoCrop: true, // 
        // autoCropWidth: 300, // 
        // autoCropHeight: 200, // 
        fixedBox: false, //  
        fixed: true, // 
        fixedNumber: [1, 1], // 
        full: true, // 
        canMoveBox: true, // 
        original: false, // 
        centerBox: false, // 
        infoTrue: true // true  false 
      }
    };
  },
  methods: {
    //base64,
    finish() {
      this.$refs.cropper.getCropData(async data => {
        this.$toast.loading({
          duration: 0, //  toast
          forbidClick: true,
          message: '...'
        });
        let newAvatar = await this.$api
          .uploadImgByBase64(data, 'avatar')
          .catch(err => {
            return err;
          });
        this.$toast.clear();
        if (newAvatar.ret == 200 && newAvatar.data.err_code == 0) {
          this.$emit('update:cutAvatarShow', false);
          this.$emit('myOriginAvatar', newAvatar.data.url);
        } else if (newAvatar.ret == 401) {
          this.$toast.fail(newAvatar.data.err_msg);
        } else {
          this.$toast(',');
        }
      });
    },
    vclose() {
      this.$emit('update:cutAvatarShow', false);
      this.$emit('myOriginAvatar', this.avatarUrl);
    }
  },
  watch: {
    cutMyAvatar() {
      this.option.img = this.cutMyAvatar;
    }
  },
  components: {
    'van-button': Button,
    VueCropper
  }
};
</script>

<style lang="scss" scoped>
@import '@/assets/css/loginAndRegister.scss';

.cropper {
  width: 100vw;
  height: 100vw;
}
</style>
